function Good(id, category, price, imgPath, amount, name, description) {
  this.id = id;
  this.category = category;
  this.price = price;
  this.imgPath = imgPath;
  this.amount = amount;
  this.name = name;
  this.description = description || '';

  Object.defineProperty(this, 'id', {
    writable: false,
    configurable: false
  });
}

Good.prototype.addUnit = function(value) {
  return this.amount += value;
};

Good.prototype.reduceUnit = function(value) {
  return this.amount -= value;
};

Good.prototype.setDescription = function(value) {
  return this.description = value;
};

function StoreInMemory() {
  this.store = [];
}

StoreInMemory.prototype.addItem = function(obj) {
  const copy = deepCopy(obj);
  for (let i = 0; i < this.store.length; i++) {
    if (this.store[i].id === obj.id) {
      return false;
    }
  }
  this.store.push(copy);
};

StoreInMemory.prototype.getItem = function(id) {
  for (let i = this.store.length - 1; i >= 0; --i) {
    if (this.store[i].id === id) {
      return this.store[i];
    }
  }
};

StoreInMemory.prototype.updateItem = function(obj) {
  for (let i = 0; i < this.store.length; i++) {
    if (this.store[i].id === obj.id) {
      this.store[i] = deepCopy(obj);
    }
  }
};

StoreInMemory.prototype.deleteItem = function(id) {
  for (let i = this.store.length - 1; i >= 0; --i) {
    if (this.store[i].id === id) {
      this.store.splice(i, 1);
    }
  }
};

function deepCopy(obj) {
  let copy;
  if (obj === null || typeof obj !== 'object') {
    return obj;
  }

  if (obj instanceof Array) {
    copy = [];
    for (let i = 0; i < obj.length; i++) {
      copy[i] = deepCopy(obj[i]);
    }
    return copy;
  }
  
  if (obj instanceof Object) {
    copy = {};
    for (const attr in obj) {
      const descr = Object.getOwnPropertyDescriptor(obj, 'id');
      if (descr) {
        Object.defineProperty(copy, 'id', descr);
      }
      if (obj.hasOwnProperty(attr)) copy[attr] = deepCopy(obj[attr]);
    }
    return copy;
  }
}

module.exports = {
  Good,
  StoreInMemory
}