function printForm(e) {
    let keyBox = document.querySelector(".input1");
    let val = keyBox.value;
    let printBlock = document.getElementById("printBlock");
    if(val == '') {
        click++;
        printBlock.textContent = click;
    } else {
        printBlock.textContent = val;
    }
}

